import classNames from "classnames";
import * as React from "react";
import type { SVGIconProps } from "../../svgIconProps";
import { IconSize } from "../../iconSize";
import * as Classes from "../../classes";
import { uniqueId } from "../../jsUtils";

export const Book: React.FC<SVGIconProps> = React.forwardRef<any, SVGIconProps>(({
    className,
    color,
    size = IconSize.STANDARD,
    title,
    htmlTitle,
    svgProps,
    tagName = "span",
    ...htmlProps
}, ref) => {
    const isLarge = size >= IconSize.LARGE;
    const pixelGridSize =  isLarge ? IconSize.LARGE : IconSize.STANDARD;
    const viewBox = `0 0 ${pixelGridSize} ${pixelGridSize}`;
    const titleId = uniqueId("iconTitle");
    const path = (
        <path
            d={
                isLarge
                    ? "M60 380V20C60 9 69 0 80 0H120V400H80C69 400 60 391 60 380zM340 400H300V240L260 280L220 240V400H140V0H340C351 0 360 9 360 20V380C360 391 351 400 340 400z"
                    : "M40 300V20C40 9 49 0 60 0H80V320H60C49 320 40 311 40 300zM260 320H240V180L200 220L160 180V320H100V0H260C271 0 280 9 280 20V300C280 311 271 320 260 320z"
            }
            fillRule="evenodd"
            transform="scale(0.05 0.05)"
        />
    );

    if (tagName === null) {
        return (
            <svg
                aria-labelledby={title ? titleId : undefined}
                data-icon="book"
                fill={color}
                height={size}
                ref={ref}
                role="img"
                viewBox={viewBox}
                width={size}
                {...svgProps}
                {...htmlProps}
            >
                {title && <title id={titleId}>{title}</title>}
                {path}
            </svg>
        );
    } else {
        return React.createElement(
            tagName,
            {
                ...htmlProps,
                "aria-hidden": title ? undefined : true,
                className: classNames(Classes.ICON, `${Classes.ICON}-book`, className),
                ref,
                title: htmlTitle,
            },
            <svg fill={color} data-icon="book" width={size} height={size} role="img" viewBox={viewBox} {...svgProps}>
                {title && <title>{title}</title>}
                {path}
            </svg>
        );
    }
});
Book.displayName = `Blueprint5.Icon.Book`;
export default Book;
