import classNames from "classnames";
import * as React from "react";
import type { SVGIconProps } from "../../svgIconProps";
import { IconSize } from "../../iconSize";
import * as Classes from "../../classes";
import { uniqueId } from "../../jsUtils";

export const Layer: React.FC<SVGIconProps> = React.forwardRef<any, SVGIconProps>(({
    className,
    color,
    size = IconSize.STANDARD,
    title,
    htmlTitle,
    svgProps,
    tagName = "span",
    ...htmlProps
}, ref) => {
    const isLarge = size >= IconSize.LARGE;
    const pixelGridSize =  isLarge ? IconSize.LARGE : IconSize.STANDARD;
    const viewBox = `0 0 ${pixelGridSize} ${pixelGridSize}`;
    const titleId = uniqueId("iconTitle");
    const path = (
        <path
            d={
                isLarge
                    ? "M390 218L210 318C206 320 204 320 200 320S194 320 190 318L10 218C4 214 0 208 0 200C0 192 4 186 10 182L190 82C194 80 196 80 200 80S206 80 210 82L390 182C396 186 400 192 400 200C400 208 396 214 390 218z"
                    : "M320 160C320 167.4 315.8 173.6 309.8 177L310 177.2L170 257.2L169.8 257C166.8 258.8 163.6 260 160 260S153.2 258.8 150.2 257L150 257.4L10 177.4L10.2 177.2C4.2 173.6 0 167.4 0 160S4.2 146.4 10.2 143C10.2 143 10 142.8 10 142.8L150 62.8L150.2 63C153.2 61.2 156.4 60 160 60S166.8 61.2 169.8 63L170 62.8L310 142.8L309.8 143C315.8 146.4 320 152.6 320 160z"
            }
            fillRule="evenodd"
            transform="scale(0.05 0.05)"
        />
    );

    if (tagName === null) {
        return (
            <svg
                aria-labelledby={title ? titleId : undefined}
                data-icon="layer"
                fill={color}
                height={size}
                ref={ref}
                role="img"
                viewBox={viewBox}
                width={size}
                {...svgProps}
                {...htmlProps}
            >
                {title && <title id={titleId}>{title}</title>}
                {path}
            </svg>
        );
    } else {
        return React.createElement(
            tagName,
            {
                ...htmlProps,
                "aria-hidden": title ? undefined : true,
                className: classNames(Classes.ICON, `${Classes.ICON}-layer`, className),
                ref,
                title: htmlTitle,
            },
            <svg fill={color} data-icon="layer" width={size} height={size} role="img" viewBox={viewBox} {...svgProps}>
                {title && <title>{title}</title>}
                {path}
            </svg>
        );
    }
});
Layer.displayName = `Blueprint5.Icon.Layer`;
export default Layer;
