import classNames from "classnames";
import * as React from "react";
import type { SVGIconProps } from "../../svgIconProps";
import { IconSize } from "../../iconSize";
import * as Classes from "../../classes";
import { uniqueId } from "../../jsUtils";

export const Duplicate: React.FC<SVGIconProps> = React.forwardRef<any, SVGIconProps>(({
    className,
    color,
    size = IconSize.STANDARD,
    title,
    htmlTitle,
    svgProps,
    tagName = "span",
    ...htmlProps
}, ref) => {
    const isLarge = size >= IconSize.LARGE;
    const pixelGridSize =  isLarge ? IconSize.LARGE : IconSize.STANDARD;
    const viewBox = `0 0 ${pixelGridSize} ${pixelGridSize}`;
    const titleId = uniqueId("iconTitle");
    const path = (
        <path
            d={
                isLarge
                    ? "M300 320H20C9 320 0 311 0 300V20C0 9 9 0 20 0H300C311 0 320 9 320 20V300C320 311 311 320 300 320zM280 40H40V280H280V40zM380 400H100C89 400 80 391 80 380V340H120V360H360V120H340V80H380C391 80 400 89 400 100V380C400 391 391 400 380 400z"
                    : "M300 320H100C89 320 80 311 80 300V260H120V280H280V140H260V100H300C311 100 320 109 320 120V300C320 311 311 320 300 320zM220 240H20C9 240 0 231 0 220V20C0 9 9 0 20 0H220C231 0 240 9 240 20V220C240 231 231 240 220 240zM200 40H40V200H200V40z"
            }
            fillRule="evenodd"
            transform="scale(0.05 0.05)"
        />
    );

    if (tagName === null) {
        return (
            <svg
                aria-labelledby={title ? titleId : undefined}
                data-icon="duplicate"
                fill={color}
                height={size}
                ref={ref}
                role="img"
                viewBox={viewBox}
                width={size}
                {...svgProps}
                {...htmlProps}
            >
                {title && <title id={titleId}>{title}</title>}
                {path}
            </svg>
        );
    } else {
        return React.createElement(
            tagName,
            {
                ...htmlProps,
                "aria-hidden": title ? undefined : true,
                className: classNames(Classes.ICON, `${Classes.ICON}-duplicate`, className),
                ref,
                title: htmlTitle,
            },
            <svg fill={color} data-icon="duplicate" width={size} height={size} role="img" viewBox={viewBox} {...svgProps}>
                {title && <title>{title}</title>}
                {path}
            </svg>
        );
    }
});
Duplicate.displayName = `Blueprint5.Icon.Duplicate`;
export default Duplicate;
