import classNames from "classnames";
import * as React from "react";
import type { SVGIconProps } from "../../svgIconProps";
import { IconSize } from "../../iconSize";
import * as Classes from "../../classes";
import { uniqueId } from "../../jsUtils";

export const Rectangle: React.FC<SVGIconProps> = React.forwardRef<any, SVGIconProps>(({
    className,
    color,
    size = IconSize.STANDARD,
    title,
    htmlTitle,
    svgProps,
    tagName = "span",
    ...htmlProps
}, ref) => {
    const isLarge = size >= IconSize.LARGE;
    const pixelGridSize =  isLarge ? IconSize.LARGE : IconSize.STANDARD;
    const viewBox = `0 0 ${pixelGridSize} ${pixelGridSize}`;
    const titleId = uniqueId("iconTitle");
    const path = (
        <path
            d={
                isLarge
                    ? "M20 320H380C391 320 400 311 400 300V100C400 89 391 80 380 80H20C9 80 0 89 0 100V300C0 311 9 320 20 320zM40 280V120H360V280H40z"
                    : "M20 260H300C311 260 320 251 320 240V80C320 69 311 60 300 60H20C9 60 0 69 0 80V240C0 251 9 260 20 260zM40 220V100H280V220H40z"
            }
            fillRule="evenodd"
            transform="scale(0.05 0.05)"
        />
    );

    if (tagName === null) {
        return (
            <svg
                aria-labelledby={title ? titleId : undefined}
                data-icon="rectangle"
                fill={color}
                height={size}
                ref={ref}
                role="img"
                viewBox={viewBox}
                width={size}
                {...svgProps}
                {...htmlProps}
            >
                {title && <title id={titleId}>{title}</title>}
                {path}
            </svg>
        );
    } else {
        return React.createElement(
            tagName,
            {
                ...htmlProps,
                "aria-hidden": title ? undefined : true,
                className: classNames(Classes.ICON, `${Classes.ICON}-rectangle`, className),
                ref,
                title: htmlTitle,
            },
            <svg fill={color} data-icon="rectangle" width={size} height={size} role="img" viewBox={viewBox} {...svgProps}>
                {title && <title>{title}</title>}
                {path}
            </svg>
        );
    }
});
Rectangle.displayName = `Blueprint5.Icon.Rectangle`;
export default Rectangle;
