import classNames from "classnames";
import * as React from "react";
import type { SVGIconProps } from "../../svgIconProps";
import { IconSize } from "../../iconSize";
import * as Classes from "../../classes";
import { uniqueId } from "../../jsUtils";

export const Desktop: React.FC<SVGIconProps> = React.forwardRef<any, SVGIconProps>(({
    className,
    color,
    size = IconSize.STANDARD,
    title,
    htmlTitle,
    svgProps,
    tagName = "span",
    ...htmlProps
}, ref) => {
    const isLarge = size >= IconSize.LARGE;
    const pixelGridSize =  isLarge ? IconSize.LARGE : IconSize.STANDARD;
    const viewBox = `0 0 ${pixelGridSize} ${pixelGridSize}`;
    const titleId = uniqueId("iconTitle");
    const path = (
        <path
            d={
                isLarge
                    ? "M380 400H20C9 400 0 391 0 380V120C0 109 9 100 20 100H133.4L123.4 40H100C89 40 80 31 80 20C80 9 89 0 100 0H300C311 0 320 9 320 20C320 31 311 40 300 40H276.6L266.6 100H380C391 100 400 109 400 120V380C400 391 391 400 380 400zM360 140H40V360H360V140z"
                    : "M300 320H20C9 320 0 311 0 300V100C0 89 9 80 20 80H115L105 40H80C69 40 60 31 60 20C60 9 69 0 80 0H240C251 0 260 9 260 20C260 31 251 40 240 40H215L205 80H300C311 80 320 89 320 100V300C320 311 311 320 300 320zM280 120H40V280H280V120z"
            }
            fillRule="evenodd"
            transform="scale(0.05 0.05)"
        />
    );

    if (tagName === null) {
        return (
            <svg
                aria-labelledby={title ? titleId : undefined}
                data-icon="desktop"
                fill={color}
                height={size}
                ref={ref}
                role="img"
                viewBox={viewBox}
                width={size}
                {...svgProps}
                {...htmlProps}
            >
                {title && <title id={titleId}>{title}</title>}
                {path}
            </svg>
        );
    } else {
        return React.createElement(
            tagName,
            {
                ...htmlProps,
                "aria-hidden": title ? undefined : true,
                className: classNames(Classes.ICON, `${Classes.ICON}-desktop`, className),
                ref,
                title: htmlTitle,
            },
            <svg fill={color} data-icon="desktop" width={size} height={size} role="img" viewBox={viewBox} {...svgProps}>
                {title && <title>{title}</title>}
                {path}
            </svg>
        );
    }
});
Desktop.displayName = `Blueprint5.Icon.Desktop`;
export default Desktop;
