import classNames from "classnames";
import * as React from "react";
import type { SVGIconProps } from "../../svgIconProps";
import { IconSize } from "../../iconSize";
import * as Classes from "../../classes";
import { uniqueId } from "../../jsUtils";

export const TickCircle: React.FC<SVGIconProps> = React.forwardRef<any, SVGIconProps>(({
    className,
    color,
    size = IconSize.STANDARD,
    title,
    htmlTitle,
    svgProps,
    tagName = "span",
    ...htmlProps
}, ref) => {
    const isLarge = size >= IconSize.LARGE;
    const pixelGridSize =  isLarge ? IconSize.LARGE : IconSize.STANDARD;
    const viewBox = `0 0 ${pixelGridSize} ${pixelGridSize}`;
    const titleId = uniqueId("iconTitle");
    const path = (
        <path
            d={
                isLarge
                    ? "M200 0C89.6 0 0 89.6 0 200S89.6 400 200 400S400 310.4 400 200S310.4 0 200 0zM300 280C294.4000000000001 280 289.4000000000001 277.8 285.8 274.2L160 148.2L114.2 194.2C110.6 197.8 105.6 200 100 200C89 200 80 191 80 180C80 174.4 82.2 169.4 85.8 165.8L145.8 105.8C149.4 102.2 154.4 100 160 100C165.6 100 170.6 102.2 174.2 105.8L314.2000000000001 245.8C317.8 249.4 320 254.4 320 260C320 271 311 280 300 280z"
                    : "M160 0C71.6 0 0 71.6 0 160S71.6 320 160 320S320 248.4 320 160S248.4 0 160 0zM240 220C234.4 220 229.4 217.8 225.8 214.2L140 128.2L94.2 174.2C90.6 177.8 85.6 180 80 180C69 180 60 171 60 160C60 154.4 62.2 149.4 65.8 145.8L125.8 85.8C129.4 82.2 134.4 80 140 80S150.6 82.2 154.2 85.8L254.2 185.8C257.8 189.4 260 194.4 260 200C260 211 251 220 240 220z"
            }
            fillRule="evenodd"
            transform="scale(0.05 0.05)"
        />
    );

    if (tagName === null) {
        return (
            <svg
                aria-labelledby={title ? titleId : undefined}
                data-icon="tick-circle"
                fill={color}
                height={size}
                ref={ref}
                role="img"
                viewBox={viewBox}
                width={size}
                {...svgProps}
                {...htmlProps}
            >
                {title && <title id={titleId}>{title}</title>}
                {path}
            </svg>
        );
    } else {
        return React.createElement(
            tagName,
            {
                ...htmlProps,
                "aria-hidden": title ? undefined : true,
                className: classNames(Classes.ICON, `${Classes.ICON}-tick-circle`, className),
                ref,
                title: htmlTitle,
            },
            <svg fill={color} data-icon="tick-circle" width={size} height={size} role="img" viewBox={viewBox} {...svgProps}>
                {title && <title>{title}</title>}
                {path}
            </svg>
        );
    }
});
TickCircle.displayName = `Blueprint5.Icon.TickCircle`;
export default TickCircle;
